<template>
  <main>
    <div class="container">
      <div class="route">
        <router-link to="/" class="route__home">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15 15">
            <path
              d="M7.028,12.906l.327-.276v7.745A.625.625,0,0,0,7.98,21H19.02a.625.625,0,0,0,.625-.625V12.63l.327.276a.625.625,0,0,0,.806-.955l-3-2.533V7.039a.625.625,0,1,0-1.25,0V8.363L13.9,6.147a.625.625,0,0,0-.806,0l-6.875,5.8a.625.625,0,1,0,.806.955ZM12.1,19.75V15.238H14.9V19.75ZM13.5,7.443l4.895,4.132V19.75H16.147V14.613a.625.625,0,0,0-.625-.625H11.478a.625.625,0,0,0-.625.625V19.75H8.605V11.575Z"
              transform="translate(-6 -6)" fill="#b2b2b2" />
          </svg>
        </router-link>
        <span class="route__path" v-for="(path, pathI) in $route.path.slice(1).split('/')" :key="pathI">
          &nbsp;/&nbsp;<router-link :to="
            '/' +
            $route.path
              .slice(1)
              .split('/')
              .slice(0, pathI + 1)
              .join('/')
          " v-text="path.replace('_', '-').toLowerCase()"></router-link>
        </span>
      </div>
      <div class="products">
        <aside class="products__filters">
          <ProductsFilter :heading="'Product Type'" :class="'products__filter--type products__filter--active'">
            <template v-slot:content>
              <div class="products__filter_row" v-for="(value, key, index) in prodsTypesCounter" :key="index">
                <label class="checkbox products__filter_input">
                  <input @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" type="checkbox" v-model="checkedTypes" :name="key" :id="key" :value="key" checked/>
                  <span class="checkbox__rect">
                    <svg xmlns="http://www.w3.org/2000/svg" width="13.371" height="11.063" viewBox="0 0 13.371 11.063">
                      <path
                        d="M11.763.25l-.876.929-6.1,6.473L2.955,5.821l-.9-.9-1.8,1.8.9.9L3.91,10.384l.929.929.9-.955,7-7.428L13.62,2Z"
                        transform="translate(-0.249 -0.25)" fill="#fff" />
                    </svg>
                  </span>
                </label>
                <label :for="key" class="products__filter_label">{{ key }} <span>({{ value.length }})</span></label>
              </div>
            </template>
          </ProductsFilter>
          <ProductsFilter :heading="'Price'" :class="'products__filter_price products__filter--active'">
            <template v-slot:content>
              <div class="range_double">
                <span class="range_double__amount range_double__amount--min">
                  <input class="range_double__value" type="number" :min="minVal" :value="varMinVal" :max="maxVal" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" @input="handleMinRange" />
                    &nbsp;USD
                </span>
                <span class="range_double__amount range_double__amount--max">
                  <input class="range_double__value" type="number" :min="minVal" :value="varMaxVal" :max="maxVal" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" @input="handleMaxRange" />
                    &nbsp;USD
                </span>
                <div class="range_double__container">
                  <div class="range_double__track"></div>
                  <input type="range" name="range_double__min" id="range_double__min" class="range_double__min" :min="minVal"
                    :max="maxVal" :value="varMinVal" @input="handleMinRange" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <input type="range" name="range_double__max" id="range_double__max" class="range_double__max" :min="minVal"
                    :max="maxVal" :value="varMaxVal" @input="handleMaxRange" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                </div>
              </div>
            </template>
          </ProductsFilter>
          <!-- <ProductsFilter :heading="'Collection'" :class="'products__filter_collection products__filter--active'">
            <template v-slot:content> </template>
          </ProductsFilter> -->
          <ProductsFilter :heading="'Size'" :class="'products__filter_size products__filter--active'">
            <template v-slot:content>
              <div class="products__filter_size__inputs">
                <div>
                  <input type="checkbox" name="s_xs" id="s_xs" v-model="checkedSizes" value="xs" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <label for="s_xs"> XS </label>
                </div>
                <div>
                  <input type="checkbox" name="s_s" id="s_s" v-model="checkedSizes" value="s" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <label for="s_s"> S </label>
                </div>
                <div>
                  <input type="checkbox" name="s_m" id="s_m" v-model="checkedSizes" value="m" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <label for="s_m"> M </label>
                </div>
                <div>
                  <input type="checkbox" name="s_l" id="s_l" v-model="checkedSizes" value="l" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <label for="s_l"> L </label>
                </div>
                <div>
                  <input type="checkbox" name="s_xl" id="s_xl" v-model="checkedSizes" value="xl" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <label for="s_xl"> XL </label>
                </div>
                <div>
                  <input type="checkbox" name="s_xll" id="s_xll" v-model="checkedSizes" value="xll" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" />
                  <label for="s_xll"> XLL </label>
                </div>
              </div>
            </template>
          </ProductsFilter>
          <ProductsFilter :heading="'Color'" :class="'products__filter_color products__filter--active'">
            <template v-slot:content>
              <div class="products__filter_color__inputs">
                <div>
                  <input type="checkbox" name="black" id="black" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" v-model="checkedColors" value="black" />
                  <label for="black">
                    <span></span>
                  </label>
                </div>
                <div>
                  <input type="checkbox" name="white" id="white" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" v-model="checkedColors" value="white" />
                  <label for="white">
                    <span></span>
                  </label>
                </div>
                <div>
                  <input type="checkbox" name="darkBlue" id="darkBlue" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" v-model="checkedColors" value="darkBlue" />
                  <label for="darkBlue">
                    <span></span>
                  </label>
                </div>
                <div>
                  <input type="checkbox" name="gray" id="gray" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" v-model="checkedColors" value="gray" />
                  <label for="gray">
                    <span></span>
                  </label>
                </div>
                <div>
                  <input type="checkbox" name="darkGreen" id="darkGreen" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" v-model="checkedColors" value="darkGreen" />
                  <label for="darkGreen">
                    <span></span>
                  </label>
                </div>
                <div>
                  <input type="checkbox" name="orange" id="orange" @change="appFilter(types = checkedTypes, min = varMinVal, max = varMaxVal, sizes = checkedSizes, colors = checkedColors, products = products)" v-model="checkedColors" value="orange" />
                  <label for="orange">
                    <span></span>
                  </label>
                </div>
              </div>
            </template>
          </ProductsFilter>
        </aside>
        <div class="products__view">
          <header class="products__view_header">
            <h1 v-if="$route.params.collection">
              {{ $route.name }}'s {{ $route.params.collection.replace('_', '-').toLowerCase() }}
            </h1>
            <h1 v-else>{{ $route.name }}</h1>
            <div class="products__view_tools">
              <div class="products__view_pagination">
                <h3>Show Products:</h3>
                <CustomSelectComponent
                :options="
                [
                  {text: '15', value: 15, actionHandler ($event) {
                    paginationNumber = $event.currentTarget.getAttribute('data-val')
                    splitProducts()
                  }},
                  {text: '50', value: 50, actionHandler ($event) {
                    paginationNumber = $event.currentTarget.getAttribute('data-val')
                    splitProducts()
                  }},
                  {text: '75', value: 75, actionHandler ($event) {
                    paginationNumber = $event.currentTarget.getAttribute('data-val')
                    splitProducts()
                  }}
                ]"/>
              </div>
              <div class="products__view_sort">
                <h3>Sort:</h3>
                <CustomSelectComponent
                :options="
                [
                  {text: 'popular', value: 'popular', actionHandler () {sortProducts('popularity')}},
                  {text: 'favourites', value: 'fav', actionHandler () {sortProducts('-fav')}},
                  {text: 'name a-z', value: 'a', actionHandler () {sortProducts('title')}},
                  {text: 'name z-a', value: 'z', actionHandler () {sortProducts('-title')}},
                  {text: 'Lowest Price', value: 'lowPrice', actionHandler () {sortProducts('price')}},
                  {text: 'Highest Price', value: 'highPrice', actionHandler () {sortProducts('-price')}},
                  {text: 'New', value: 'new', actionHandler () {sortProducts('-created_at')}}
                  ]" />
              </div>
            </div>
            <div class="products__filter_icon">
              <svg xmlns="http://www.w3.org/2000/svg" width="22" height="15.613" viewBox="0 0 22 15.613">
                <path
                  d="M7.118,0A3.678,3.678,0,0,0,3.539,2.843H.845a.845.845,0,1,0,0,1.69H3.539a3.678,3.678,0,0,0,7.158,0H21.155a.845.845,0,1,0,0-1.69H10.7A3.678,3.678,0,0,0,7.118,0Zm0,5.686A1.988,1.988,0,1,1,9.106,3.7,1.988,1.988,0,0,1,7.118,5.686Z"
                  transform="translate(0)" />
                <path
                  d="M14.912,8.3a3.678,3.678,0,0,0-3.579,2.843H.845a.845.845,0,1,0,0,1.69H11.3a3.678,3.678,0,0,0,7.158,0h2.694a.845.845,0,0,0,0-1.69H18.461A3.678,3.678,0,0,0,14.912,8.3Zm0,5.667A1.988,1.988,0,1,1,16.9,11.978,1.988,1.988,0,0,1,14.912,13.967Z"
                  transform="translate(0 -0.049)" />
              </svg>
            </div>
          </header>
          <div class="products__content">
            <div v-for="(product, productI) in visibleProducts[activePagination]" :key="productI">
              <ProductComponent :prod="product" />
            </div>
            <template></template>
          </div>
          <div v-if="getFail" class="alert alert-danger">An Error has araised</div>
          <div v-if="!avaiProds" class="alert alert-danger">There is no products here right now</div>
          <div v-if="!filterResults && avaiProds && !getFail && loadDone" class="alert alert-warning">Your Filteration Results doesn't exist</div>
          <div class="spinner" v-if="!loadDone"></div>
          <div class="products__pagination pagination" v-if="products !== null">
            <ul>
              <template v-for="(item, index) in paginationCount" :key="index">
                <li :class="index === activePagination ? 'pagination__active' : ''"><a :href="`#p${item}`" v-text="item" @click.prevent="activePagination = index"></a></li>
                <li v-if="item === paginationCount / 2 && paginationCount > maxPaginationCount"><a href="#">...</a></li>
              </template>
            </ul>
          </div>
        </div>
      </div>
      <NewsletterComponent class="mt-3"/>
    </div>
  </main>
</template>
<script>
// @ is an alias to /src
// import HeaderComponent from '@/components/HeaderComponent.vue'

import ProductsFilter from '@/components/ProductsFilterComponent.vue'
import ProductComponent from '@/components/ProductComponent.vue'
import NewsletterComponent from '@/components/NewsletterComponent.vue'
import CustomSelectComponent from '@/components/CustomSelectComponent.vue'
export default {
  name: 'ShopView',
  data () {
    return {
      threshold: 5,
      minVal: 0,
      maxVal: 500,
      varMinVal: 0,
      varMaxVal: 500,
      products: [],
      productsClone: [],
      visibleProducts: [],
      getFail: false,
      avaiProds: true,
      loadDone: false,
      filterResults: null,
      prodsTypesCounter: {},
      checkedTypes: [],
      checkedSizes: [],
      checkedColors: [],
      sortType: 'popular',
      paginationNumber: 15,
      activePagination: 0,
      maxPaginationCount: 7,
      apiDomain: 'https://api.npoint.io/579eef7caed076ac7647/'
      // apiDomain: 'http://localhost:3000/'
    }
  },
  props: ['collection', 'type'],
  components: {
    ProductsFilter,
    ProductComponent,
    NewsletterComponent,
    CustomSelectComponent
  },
  computed: {
    paginationCount () {
      return Math.ceil(this.productsClone.length / this.paginationNumber)
    }
  },
  mounted () {
    console.log(this.$route)
    this.$watch(
      () => this.$route.params,
      () => {
        if (!this.$route.path.includes('shop')) return
        this.fetchProducts(`${this.apiDomain}shop/${this.$route.name}`, true)
      },
      // fetch the data when the view is created and the data is
      // already being observed
      { immediate: true }
    )
    this.setBackgroundTrack()
  },
  methods: {
    test () {
      console.log('Test From Shop !')
    },
    fetchProducts (url, meta = false) {
      fetch(url)
        .then(res => res.json())
        .then(data => {
          this.products = data
          this.productsClone = data
          // Divide visible Products into Array[N] of objects WHICH N = Pagination Count
          // Array starting points => [0N, 1N, 2N, 3N, ..., xN], x = ? -> Pagination Count
          this.visibleProducts = []
          for (let i = 0; i < this.paginationCount; i++) {
            this.visibleProducts[i] = this.productsClone.slice(i * this.paginationCount, this.paginationNumber)
          }
          // this.appFilter(this.checkedTypes, this.varMinVal, this.varMaxVal, this.checkedSizes, this.checkedColors, this.products)
          this.avaiProds = this.products.length > 0
          this.loadDone = true
          // Check if all products are returned, then make meta data
          if (meta) {
            this.prodsTypesCounter = {}
            // declare array that will contains all types
            const prodsTypes = []
            /* Get All Given  Products Types in data */
            // Loop through all products array
            data.forEach(element => {
              // Loop through each product object
              for (const prop in element) {
                // check if Looped prop is type, then store its value
                if (prop === 'type') {
                  // add type value to array
                  if (!prodsTypes.includes(element[prop])) prodsTypes.push(element[prop])
                  if (this.prodsTypesCounter[element[prop]]) {
                    if (!this.prodsTypesCounter[element[prop]].includes(element)) this.prodsTypesCounter[element[prop]].push(element)
                  } else {
                    this.prodsTypesCounter[element[prop]] = new Array(element)
                  }
                }
              }
            })
            /* *** */
          }
          this.appFilter()
        })
        .catch((err) => {
          console.error(err.message)
          this.getFail = true
          this.loadDone = true
        })
    },
    toggleFilter (e) {
      e.currentTarget.parentElement.classList.toggle('products__filter--active')
    },
    handleMinRange (e) {
      if (parseInt(e.target.value) > parseInt(this.varMaxVal) - this.threshold) {
        e.target.value = parseInt(this.varMaxVal) - this.threshold
      }
      this.varMinVal = parseInt(e.target.value)
      console.log('handleMinRange() and trigger is')
      console.log(e.target)
      console.log(`and varMinVal is: ${this.varMinVal}`)
      this.setBackgroundTrack()
    },
    handleMaxRange (e) {
      if (parseInt(e.target.value) < parseInt(this.varMinVal) + this.threshold) {
        e.target.value = parseInt(this.varMinVal) + this.threshold
      }
      this.varMaxVal = parseInt(e.target.value)
      this.setBackgroundTrack()
    },
    setBackgroundTrack (
      start = parseInt(this.varMinVal),
      end = parseInt(this.varMaxVal)
    ) {
      document.querySelector(
        '.range_double__track'
      ).style.background = `linear-gradient(to right, #CECECE 0 ${start / 5
      }%, #000 ${start / 5}% ${end / 5}%, #CECECE ${end / 5}% 100%)`
    },
    /* !!! 0 Callback !!! */
    filterProductsType (e) {
      const selectedInps = []
      let queries = '';
      [...document.querySelectorAll('.products__filter--type input[type="checkbox"]')].forEach(element => {
        if (element.checked) selectedInps.push(element.value)
      })
      // Generate Query
      if (selectedInps.length > 0) {
        selectedInps.forEach((query, queryI) => {
          // check if first elmnt, then add ? else add &
          if (queryI === 0) {
            queries = `?type=${query}`
          } else {
            queries += `&type=${query}`
          }
        })
        // Fetch data with selected queries
        this.fetchProducts(`${this.apiDomain}${this.$route.name + queries}`)
      } else {
        this.fetchProducts(`${this.apiDomain}${this.$route.name}`)
      }
      console.log(queries)
    },
    typeFilter (types = this.checkedTypes, products = this.products) {
      if (types.length < 1) types = Object.keys(this.prodsTypesCounter)
      const result = []
      // Loop for each type
      types.forEach(type => {
        // for each type loop for each product in products array
        products.forEach((product, i, arr) => {
          if (type === product.type) {
            result.push(product)
          }
        })
      })
      return result
    },
    priceFilter (min = this.varMinVal, max = this.varMaxVal, products = this.products) {
      const result = []
      // Loop through all products
      products.forEach(product => {
        // Check if price is in range, Then add it
        if (product.price >= min && product.price <= max) result.push(product)
      })
      return result
    },
    sizeFilter (sizes = this.checkedSizes, products = this.products) {
      const result = []
      // Loop through products and check for the Product that contains given sizes
      products.forEach(product => {
        for (let i = 0; i < product.sizes.length; i++) {
          if (sizes.includes(product.sizes[i])) {
            result.push(product)
            break
          }
        }
      })
      return result
    },
    colorFilter (colors = this.checkedColors, products = this.products) {
      const result = []
      // Loop for each type
      colors.forEach(color => {
        // for each type loop for each product in products array
        products.forEach((product, i, arr) => {
          if (color === product.color) {
            result.push(product)
          }
        })
      })
      return result
    },
    sortProducts (sortType = this.sortType) {
      if (this.productsClone.length === 0) {
        this.productsClone = []
        this.visibleProducts = []
        return
      }
      this.sortType = sortType
      this.productsClone.sort(this.dynamicSort(sortType))
      this.splitProducts()
    },
    splitProducts () {
      for (let i = 0; i < this.paginationCount; i++) {
        this.visibleProducts[i] = this.productsClone.slice(i * this.paginationNumber, (i * this.paginationNumber) + this.paginationNumber)
      }
    },
    dynamicSort (property) {
      let sortOrder = 1
      if (property[0] === '-') {
        sortOrder = -1
        property = property.substr(1)
      }
      return function (a, b) {
        const result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0
        return result * sortOrder
      }
    },
    appFilter (types = this.checkedTypes, min = this.varMinVal, max = this.varMaxVal, sizes = this.checkedSizes, colors = this.checkedColors, products = this.products) {
      if (sizes.length < 1) sizes = ['xs', 's', 'm', 'l', 'xl', 'xll']
      if (colors.length < 1) colors = ['black', 'white', 'darkBlue', 'gray', 'darkGreen', 'orange']
      const priceTypeResults = []
      const colorSizeResults = []
      const result = []
      // check for intersections Between Price and Type and push to priceTypeResults array
      this.typeFilter(types, products).forEach(type => {
        this.priceFilter(min, max).forEach(price => {
          if (type === price) priceTypeResults.push(type)
        })
      })
      // check for intersections Between Color and Size and push to colorSizeResults array
      this.sizeFilter(sizes, products).forEach(size => {
        this.colorFilter(colors).forEach(color => {
          if (size === color) colorSizeResults.push(size)
        })
      })
      // check for intersections Between [price, type] array and [size, push] array and push to result array
      priceTypeResults.forEach(i1 => {
        colorSizeResults.forEach(i2 => {
          if (i1 === i2) result.push(i1)
        })
      })
      // console.log('appFilter says: result = ', result)
      this.productsClone = result
      console.log('appFilter says: this.productsClone = ', this.productsClone)
      this.filterResults = result.length
      this.sortProducts()
      // Recalculate pagination depending on productsClone
    },
    paginationToggle (e) {
      [...e.currentTarget.parentElement.parentElement.querySelectorAll('li')].forEach(item => {
        item.classList.remove('pagination__active')
      })
    }
  }
}
</script>
