<template>
    <div class="custom-select" @click="toggleSelect">
        <div class="custom-select__arrow">
            <svg xmlns="http://www.w3.org/2000/svg" width="7.414" height="4.414" viewBox="0 0 7.414 4.414">
                <g transform="translate(25.707 45.207) rotate(180)">
                    <line x2="3" y2="3" transform="translate(22 41.5)" fill="none" />
                    <line x1="3" y2="3" transform="translate(19 41.5)" fill="none" />
                </g>
            </svg>
        </div>
        <span class="custom-select__active" v-text="options[0].text" :data-val="options[0].value"></span>
        <ul class="custom-select__options">
            <li class="products__view_cust-option custom-select__option" v-for="(value, index) in options" :key="index"
                :data-val="value.value" v-text="value.text" @click="setVal($event), value.actionHandler($event)"></li>
        </ul>
    </div>
</template>

<script>
export default {
  name: 'CustomSelectComponent',
  props: ['options'],
  methods: {
    toggleSelect (e) {
      e.currentTarget.classList.toggle('custom-select--active')
    },
    setVal (e) {
      e.currentTarget.parentElement.parentElement.querySelector('.custom-select__active').textContent = e.currentTarget.textContent
      console.log(e.currentTarget.parentElement.parentElement.querySelector('.custom-select__active'))
      e.currentTarget.parentElement.parentElement.querySelector('.custom-select__active').setAttribute('data-val', e.currentTarget.getAttribute('data-val'))
    },
    test () {
      console.log('Test from custom select')
    }
  },
  mounted () {
    document.addEventListener('click', (e) => {
      if (!e.target.closest('.custom-select')) {
        [...document.querySelectorAll('.custom-select')].forEach(elmnt => {
          elmnt.classList.remove('custom-select--active')
        })
      }
    })
  }
}
</script>
